header {
    position: relative;
    overflow: hidden;
}

header img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;

}
footer span {
    color: #ac8573;
}

footer p {
    font-size: 14px;
}
.main {
    padding: 0 30px 30px 0;
}

.main__list {
    list-style: none;
}

.main__listItem {
    display: block;
    text-decoration: none;
    color: #222;
    text-transform: uppercase;
    font-weight: bold;
    padding: 10px;
    transition: .3s;
}

.main__listItem:hover {
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
}


.main__listItem.active {
    border-left: 10px solid #ac8573;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}

.main__list li:nth-child(1) .main__listItem.active {
    border-left-color: #c2cea9;
}

.main__list li:nth-child(2) .main__listItem.active {
    border-left-color: #a691a0;
}

.main__list li:nth-child(3) .main__listItem.active {
    border-left-color: #ac8573;
}

.main__list li:nth-child(4) .main__listItem.active {
    border-left-color: #4b5b2a;
}
article {
    margin-top: 40px;
}

article h3 {
    margin-bottom: 3px;
    text-transform: uppercase;
}

article span {
    display: block;
    margin-bottom: 10px;
    font-size: 14px;
}

article p {
    font-size: 16px;
}
.products h2 {
    margin-bottom: 20px;
    text-transform: uppercase;
}

.products ul {

    list-style: none;
    padding-left: 30px;
}

.products li {
    margin-bottom: 20px;

}

.products a {
    text-decoration: none;
    text-transform: capitalize;
    color: #333;
    transition: .2s;
}

.products a:hover {
    color: #a691a0;
}
.contact textarea {
    margin: 20px 0;
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    line-height: 1.6;
    border-radius: 5px;
    box-shadow: 0 0 4px 0;
}

.contact button {
    display: block;
    margin: 0 20% 0 auto;
    border: 2px solid #222;
    border-radius: 5px;
    background-color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 20px;
    transition: .2s;

}

.contact button:hover {
    background-color: #c2cea9;

}
.form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 50%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.login {
    display: block;
    margin-top: 20px;
}

.login input {
    margin-left: 10px;
    height: 20px;
    max-width: 550px;
    border: 1px solid #222;
    border-radius: 2px;
    box-shadow: 0 0 4px 0;
}

.login--button {
    display: block;
    margin: 30px 0 0 34%;
    font-size: 20px;
    background-color: #fff;
    border: 1px solid #000;
    padding: 5px 10px;
    border-radius: 5px;
}

.login--button:hover {
    background-color: #c2cea9;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: 'open sans', sans-serif;
    font-size: 20px;
}

.app {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: 100vh;
    width: 90%;
    margin: 0 auto;
    background-color: #ddd;
    color: #222;
}

header {
    -webkit-flex-basis: 30vh;
            flex-basis: 30vh;
}

main {
    padding: 30px 0;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    display: -webkit-flex;
    display: flex;
}

aside {
    min-width: 240px;
}

.page {
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

footer {
    background-color: #c2cea9;
    padding: 10px 0;
    text-align: center;
    text-transform: uppercase;
    color: #222;

}

.product-site {
    color: #222;
    text-transform: uppercase;
    font-weight: 700;
}

.product__item {
    text-transform: capitalize;
    margin-bottom: 10px;
}

.product-link {
    text-decoration: none;
    color: #222;
    font-size: 16px;
    transition: .2s;
}

.product-link:hover {
    color: #a691a0;
}
